.cross {
  position: relative;
  width: 72px;
  height: 72px; }
  .cross .line1,
  .cross .line2 {
    border: 1px solid #fff;
    width: 28px;
    height: 1px;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .cross .line1 {
    transform: rotate(45deg); }
  .cross .line2 {
    transform: rotate(-45deg); }

